import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';

export default function PageTitle() {
  const { pathname } = useLocation();

  useEffect(() => {
    switch (pathname) {
      case '/':
        document.title = ' - STR';
        break;
      case '/community':
        document.title = ' - STR';
        break;
      case '/profile':
        document.title = ' - STR';
        break;
      default:
        document.title = 'STR';
    }
  }, [pathname]);

  return null;
}
